.menu-item{
    display: flex;
    align-items: center;
    font-size: 1.8rem;
    font-weight: 700;
    padding: 8px;
    border-radius: 4px;
    transition: background-color ease-in-out 200ms;
    color: rgb(207, 107, 49)  !important ; 
    &.active{
        color: #038797 !important;
    }
    
    &:hover{
        background-color: rgba(22,24,35,0.03);
    }
}
.title{
    margin-left: 10px;
}