.wrapper{
    display: flex;
    align-items: center;
    padding: 9px 16px;
    cursor: pointer;
    font-family: 'ProximaNova', sans-serif;

    &:hover{
        background-color: rgba(22,24,35,0.03);
    }
}
.avatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.info{
    flex: 1;
    margin-left: 12px;
}
.name{
    font-size: 1.4rem;
    font-weight: 600;
}
.username{
    font-size: 1.4rem;
    color: rgba(22,24,35,0.5);
}
.check{
    margin-left: 8px;
    font-size: 1.2rem;
    color: rgb(32,213, 236);
}